.slant {
    -webkit-filter: drop-shadow(3px 3px 3px black);
            filter: drop-shadow(3px 3px 3px black);
}

.slant-left{
    position: relative;
    float: left;
    height: 450px;
    width: 200px;
    background-image: url(/portfolio-project/static/media/img1.0a763cf3.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    -webkit-clip-path: polygon(0 0, 100% 0, 75% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 75% 100%, 0 100%);
    shape-outside: polygon(0 0, 100% 0, 75% 100%, 0 100%);
    transform: translate(-10vw );
    margin-right: -8vw;
    margin-bottom: 10px;
    z-index: 0;
}

.slant-right{
    position: relative;
    float: right;
    height: 400px;
    width: 200px;
    background-image: url(/portfolio-project/static/media/sd1.32e41c17.jpg);
    background-repeat: no-repeat;
    background-position: 87% 50%;
    background-size: cover;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
    shape-outside: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
    transform: translate(10vw);
    margin-left: -8vw;
    margin-bottom: 10px;
    z-index: 0;
}

.slant-bottom{
    position: relative;
    float: left;
    height: 400px;
    width: 200px;
    background-image: url(/portfolio-project/static/media/philly1.077d0e33.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    -webkit-clip-path: polygon(0 0, 75% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 75% 0, 100% 100%, 0 100%);
    shape-outside:polygon(0 0, 75% 0, 100% 100%, 0 100%);
