}

.row {
    display: none;
}

.display-lg {
    opacity: 1;
    width: 170px;
    height: 240px;
    box-shadow: 5px 5px 5px black;
}

.display-lg-2 {
    opacity: 1;
    width: 350px;
    height: 240px;
    box-shadow: 5px 5px 5px black;
}

.display-lg-3 {
    opacity: 1;
    width: 300px;
    height: 300px;
    box-shadow: 5px 5px 5px black;
    margin-bottom: 20px;
}

@media only screen and (min-width: 768px) {
    .slant {
        display: none;
    }

    .row {
        display: flex;
        justify-content: center;
    }
    .about-content .first {
        min-height: unset;
    }
}
.section-title {
    color: var(--color-main);
}
.projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media only screen and (min-width: 996px) {
    .projects {
        flex-direction: row;
        justify-content: space-center
    }
}
.project-wrapper {
    position: relative;
    max-width: 250px;
    min-width: 250px;
    margin: 10px 10px;
    height: 250px;
    cursor: pointer;
    transform-style: preserve-3d;
    transition: all 300ms ease-in-out;
}

.project-wrapper-front {
    position: absolute;
    max-width: 100%;
    min-width: 100%;
    height:100%;
    box-shadow: 10px 10px 10px black;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(0deg);
}

.project-wrapper-back {
    position: absolute;
    max-width: 100%;
    min-width: 100%;
    height:100%;
    box-shadow: 10px 10px 10px black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(180deg);
}

img {
    opacity: .5;
    width: 250px; 
    height: 250px;
    transition: all 300ms ease-in-out;
}
img:hover {
    opacity: .9;
}

@media only screen and (min-width: 1200px) {
    .project-wrapper {
        max-width: 300px;
        min-width: 300px;
        height: 300px;
    }
    img {
        width: 300px; 
        height: 300px;
    }
    .flip {
        height: 100px;
    }
}

.flip {
    transform: rotateY(180deg);
    height: 350px;
}

.tile-title {
    position: absolute;
    z-index: 11;
    color: white;
    text-shadow: 2px 2px 2px black;
    width: 225px;
    text-align: center;
}

.link-list {
    padding-top: 10px;
    color: var(--color-text);
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.link {
    margin: 0 10px;
}

@media only screen and (min-width: 996px) {
    .link {
        margin: 0 30px;
    }
}
